<template>

	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择金额总预算</p>
		<div class="budgutContainer" v-bind:class="budgetColor">
			<div class="price" v-bind:class="{'active':price.checked == 1}" v-for="(price,index) in budGet" v-on:click="clickPrice(index,price.id)">
				<div class="priceinner"><span>{{ price.des }}</span></div>
			</div>
		</div>
		<a class="next-subject" href="#/question5">NEXT</a>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				num: 4,
				budGet: [{
						id: 1,
						des: '1000元以内',
						checked: 0
					},
					{
						id: 2,
						des: '1000-2000元',
						checked: 0
					},
					{
						id: 3,
						des: '2000-5000元',
						checked: 0
					},
					{
						id: 4,
						des: '5000元以上',
						checked: 0
					}
				]
			}
		},
		computed: {
			budgetColor() {
				var budgetColorClassName = 'budgetColorClass';
				for(var i = 0; i < this.budGet.length; i++) {

					if(this.budGet[i].checked == 1) {
						budgetColorClassName = budgetColorClassName + i;
					};
				}
				return budgetColorClassName;
			},
			//获取全局预算Id
			SrorebudGetColor() {
				this.checkedbudGet = this.$store.state.budGet;
				for(var i = 0; i < this.budGet.length; i++) {
					if(this.checkedbudGet == this.budGet[i].id) {
						this.budGet[i].checked = 1;
					}
				}
				return this.checkedbudGet
			}
		},
		methods: {
			clickPrice(index, id) {
				for(var i = 0; i < this.budGet.length; i++) {

					this.budGet[i].checked = 0;
				}
				this.budGet[index].checked = !this.budGet[index].checked;

				this.selectBudGet(id)
			},
			selectBudGet(id) {
				this.$store.commit('changeBudGet', id);
			}
		}
	}
</script>

<style scoped>
	.pagebox {
		padding: 0;
		margin: 30px;
		position: relative;
	}
	
	.budgutContainer {
		position: absolute;
		top: 65px;
		left: 10%;
		margin-left: -20px;
		width: 80%;
		padding-top: 80%;
		border-top: 20px solid rgba(255, 255, 255, .3);
		border-bottom: 20px solid rgba(255, 255, 255, .3);
		border-left: 20px solid rgba(255, 255, 255, .5);
		border-right: 20px solid rgba(255, 255, 255, .5);
		border-radius: 50%;
	}
	
	.budgetColorClass0 {
		border-top: 20px solid transparent;
	}
	
	.budgetColorClass1 {
		border-right: 20px solid transparent;
	}
	
	.budgetColorClass3 {
		border-bottom: 20px solid transparent;
	}
	
	.budgetColorClass2 {
		border-left: 20px solid transparent;
	}
	
	.price.active {
		color: #FFF;
		border: 3px solid #e8e026;
	}
	
	.price.active .priceinner {
		background: none;
	}
	
	.price .priceinner span {
		padding: 5px;
		display: table-cell;
		vertical-align: middle;
	}
	
	.price {
		width: 40%;
		height: 40%;
		border: 5px solid rgba(255, 255, 255, .2);
		box-sizing: border-box;
		color: #000;
		overflow: hidden;
		border-radius: 50%;
		position: absolute;
	}
	
	.price .priceinner {
		display: table;
		padding: 10px;
		background: #e8e026;
		height: 104%;
		margin-top: -2%;
		width: 100%;
		margin-left: -10px;
	}
	
	.price:nth-child(1) {
		position: absolute;
		top: -20%;
		left: 30%;
	}
	
	.price:nth-child(2) {
		position: absolute;
		top: 30%;
		right: -20%;
	}
	
	.price:nth-child(3) {
		position: absolute;
		top: 30%;
		left: -20%;
	}
	
	.price:nth-child(4) {
		position: absolute;
		bottom: -20%;
		left: 30%;
	}
</style>


