<template>
	<div class="pagebox">
		<p class="question-subject"><span class="questionNum">{{ num }}</span>请选择场合</p>
		<div class="situationBox">

			<div class="situation" :class="{'selected':item.checked}" v-for="(item,index) in situations" v-on:click="selectSituation(index,item.id)">
				<p>{{ item.des }}</p>
				<div class="innerimage"><img v-bind:src="'static/image/q3a'+item.id+'.png'" /></div>
			</div>

			<div class="bgbox">
				<img src="static/image/q3bg.png" />
			</div>
		</div>
		<a class="next-subject" href="#/question4">NEXT</a>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				num: 3,
				situations: [{
						id: "1",
						des: "正式商务",
						checked: 0
					},
					{
						id: "2",
						des: "日常上班",
						checked: 0
					},
					{
						id: "3",
						des: "休闲",
						checked: 0
					},
					{
						id: "4",
						des: "上班偏休闲",
						checked: 0
					},
					{
						id: "5",
						des: "旅行",
						checked: 0
					},
					{
						id: "6",
						des: "PARTY",
						checked: 0
					},

				]
			}
		},
		computed: {
			StoreSituation() {
				return this.$store.state.Situation
			}
		},
		created: function() {
			for(var i = 0; i < this.situations.length; i++) {
				for(var j = 0; j < this.StoreSituation.length; j++) {
					if(this.situations[i].id == this.StoreSituation[j]) {
						this.situations[i].checked = 1;
					}
				}
			}
		},
		methods: {
			selectSituation(index, id) {

				this.situations[index].checked = !this.situations[index].checked

				var selectSituation = [];
				for(var i = 0; i < this.situations.length; i++) {
					if(this.situations[i].checked) {
						selectSituation.push(this.situations[i].id)
					}

				}

				this.$store.commit('changeSituation', selectSituation);

			}
		}

	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.situationBox {
		width: 80%;
		margin: 30% auto;
		position: relative;
	}
	
	.bgbox {
		position: absolute;
		width: 120%;
		padding-top: 120%;
		top: -40%;
		left: -10%;
		border: 1px solid #e59814;
		border-radius: 50%;
		box-sizing: border-box;
	}
	
	.bgbox img {
		width: 30%;
		position: absolute;
		bottom: -18%;
		left: 35%;
	}
	
	.situation {
		z-index: 1000;
		width: 30%;
		box-sizing: border-box;
		padding-top: 30%;
		float: left;
		position: relative;
		margin-bottom: 10px;
	}
	
	.situationBox:after {
		content: "";
		display: block;
		clear: both;
	}
	
	.innerimage {
		border: 1px solid #FFF;
		border-radius: 50%;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}
	
	.situation img {
		position: absolute;
		top: 20%;
		left: 0;
		right: 0;
		height: 60%;
		display: block;
		margin: 0 auto;
	}
	
	.situation:nth-child(1),
	.situation:nth-child(2),
	.situation:nth-child(4),
	.situation:nth-child(5) {
		margin-right: 5%;
	}
	
	.situation {
		transform: rotateY(180deg);
		transition: 1s all;
	}
	
	.situation p {
		position: absolute;
		top: 40%;
		width: 100%;
		color: #e59814;
		display: none;
	}
	
	.situation.selected {
		transform: rotateY(0deg);
		transition: 1s all;
	}
	
	.situation.selected .innerimage {
		border: none;
		background: rgba(255, 255, 255, .2);
	}
	
	.situation.selected p {
		display: block;
	}
	
	.situation.selected img {
		display: none;
	}
</style>


