import Vue from 'vue'
import Router from 'vue-router'
import Question1 from '@/components/question1'
import Question2 from '@/components/question2'
import Question3 from '@/components/question3'
import Question4 from '@/components/question4'
import Question5 from '@/components/question5'
import Question6 from '@/components/question6'
import Question7 from '@/components/question7'
import Question8 from '@/components/question8'
import Question9 from '@/components/question9'
import Question10 from '@/components/question10'
import Product from '@/components/product'
import DisProduct from '@/components/disproduct'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Question_1',
      component: Question1
    },
    {
      path: '/question2',
      name: 'Question_2',
      component: Question2,
     
    },
    {
      path: '/question3',
      name: 'Question_3',
      component: Question3
    },
    {
      path: '/question4',
      name: 'Question_4',
      component: Question4
    },
    {
      path: '/question5',
      name: 'Question_5',
      component: Question5
    },
    {
      path: '/question6',
      name: 'Question_6',
      component: Question6
    },
    {
      path: '/question7',
      name: 'Question_7',
      component: Question7
    },
    {
      path: '/question8',
      name: 'Question_8',
      component: Question8
    },
    {
      path: '/question9',
      name: 'Question_9',
      component: Question9
    },
    {
      path: '/question10',
      name: 'Question_10',
      component: Question10
    },
    {
      path: '/product',
      name: 'Product',
      component: Product
    },
    {
      path: '/disproduct',
      name: 'DisProduct',
      component: DisProduct
    }
    
  ]
})



